import React, { useState } from 'react';
import { Deal } from './types';

interface DealCardProps {
  deal: Deal;
  isDragging?: boolean;
  onClick: (deal: Deal) => void;
  onDragStart: (dealId: string) => void;
  onDragEnd: () => void;
  draggable?: boolean;
}

export function DealCard({ deal, isDragging, onClick, onDragStart, onDragEnd, draggable = true }: DealCardProps) {
  const [hasDragged, setHasDragged] = useState(false);
  const [dragStartTime, setDragStartTime] = useState<number | null>(null);
  const [dragEndTime, setDragEndTime] = useState<number | null>(null);

  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    
    const now = Date.now();
    
    // MIGLIORATO: Prevenzione click pi robusta
    // 1. Non cliccare se abbiamo appena fatto drag
    if (hasDragged) {
      console.log(`DealCard: Click prevented for ${deal.id} - hasDragged is true`);
      return;
    }
    
    // 2. Non cliccare se il drag  iniziato di recente
    if (dragStartTime && now - dragStartTime < 800) {
      console.log(`DealCard: Click prevented for ${deal.id} - recent drag start`);
      return;
    }
    
    // 3. Non cliccare se il drag  finito di recente
    if (dragEndTime && now - dragEndTime < 800) {
      console.log(`DealCard: Click prevented for ${deal.id} - recent drag end`);
      return;
    }
    
    console.log(`DealCard: Click allowed for ${deal.id}`);
    onClick(deal);
  };

  const handleDragStart = (e: React.DragEvent) => {
    const now = Date.now();
    e.dataTransfer.setData('text/plain', deal.id);
    e.dataTransfer.effectAllowed = 'move';
    
    setHasDragged(true);
    setDragStartTime(now);
    setDragEndTime(null); // Reset drag end time
    
    console.log(`DealCard: Drag started for ${deal.id}`);
    onDragStart(deal.id);
  };

  const handleDragEnd = (e: React.DragEvent) => {
    const now = Date.now();
    
    console.log(`DealCard: Drag ended for ${deal.id}`);
    onDragEnd();
    
    setDragEndTime(now);
    
    // Reset hasDragged dopo un delay pi lungo per essere sicuri
    setTimeout(() => {
      setHasDragged(false);
      setDragStartTime(null);
    }, 1200); // Aumentato da 500ms a 1200ms
  };

  const handleDragOver = (e: React.DragEvent) => {
    // Prevent default to allow drop
    e.preventDefault();
  };

  return (
    <div
      draggable={draggable}
      className={`kanban-card ${isDragging ? 'dragging' : ''} ${!draggable ? 'not-draggable' : ''}`}
      data-source={deal.source || ''}
      onClick={handleClick}
      onDragStart={draggable ? handleDragStart : undefined}
      onDragEnd={draggable ? handleDragEnd : undefined}
      onDragOver={handleDragOver}
      style={{ 
        opacity: isDragging ? 0.6 : (!draggable ? 0.7 : 1),
        transition: 'all 0.2s ease-in-out',
        cursor: draggable ? 'move' : 'pointer'
      }}
    >
      {/* Deal Card Header */}
      <div className="kanban-card-header">
        <h4 className="kanban-card-title">{deal.company_name || 'Unknown Company'}</h4>
        {deal.product_type && (
          <div className="kanban-card-priority">
            <span className="priority-icon"></span>
          </div>
        )}
      </div>

      {/* Deal ID as subtitle */}
      <div className="kanban-card-content">
        <p style={{ fontSize: '12px', fontWeight: '600', color: '#3498db', marginBottom: '8px' }}>
          {deal.deal_id || 'No ID'}
        </p>
        {deal.product_type && (
          <div style={{ marginBottom: '8px' }}>
            <span 
              className="kanban-card-tag" 
              style={{ 
                background: 'linear-gradient(135deg, #3498db, #2980b9)',
                color: 'white',
                fontWeight: '600'
              }}
            >
              {deal.product_type}
            </span>
          </div>
        )}
      </div>

      {/* Deal Footer */}
      <div className="kanban-card-footer">
        {deal.underwriter && (
          <div className="kanban-card-assignee">
            <span className="assignee-avatar"></span>
            <span className="assignee-name">{deal.underwriter}</span>
          </div>
        )}
        {deal.date && (
          <div className="kanban-card-date">
            <span className="date-icon"></span>
            <span className="date-text">{deal.date}</span>
          </div>
        )}
      </div>

      {/* Custom HTML content */}
      {deal.custom_html && (
        <div 
          style={{ 
            marginTop: '12px', 
            paddingTop: '8px', 
            borderTop: '1px solid #e0e0e0' 
          }}
          dangerouslySetInnerHTML={{ __html: deal.custom_html }}
        />
      )}
    </div>
  );
} 