import {
  Streamlit,
  withStreamlitConnection,
  ComponentProps
} from "streamlit-component-lib"
import React, { ReactNode, useState, useEffect, useRef, useCallback } from "react"

import { DealCard } from "./DealCard"
import { FilterPanel, FilterOptions, SortOptions } from "./FilterPanel"
import './KanbanComponent.css'
import { Deal, Stage, UserInfo, DragRestrictions, ValidationError, PermissionMatrix, BusinessRule, DragFeedback } from './types';
import { PermissionEngine } from './PermissionEngine';

// Interface per tracciare gli aggiornamenti ottimistici
interface PendingUpdate {
  dealId: string;
  fromStage: string;
  toStage: string;
  timestamp: number;
}

interface ColumnProps {
  stage: Stage,
  deals: Deal[],
  onDealClick: (deal: Deal) => void,
  onDrop: (dealId: string, stageId: string) => void,
  children?: ReactNode,
  canDropHere?: boolean,
  draggableStages?: string[],
  isDragActive?: boolean,
  dragFeedback?: DragFeedback,
  userInfo?: UserInfo,
  showTooltip?: boolean
}

function KanbanColumn(props: ColumnProps) {
  const [isDragOver, setIsDragOver] = useState(false);
  const [tooltipVisible, setTooltipVisible] = useState(false);
  const [tooltipPosition, setTooltipPosition] = useState({ x: 0, y: 0 });
  
  // const canDrop = props.canDropHere !== false; // Default to true if not specified
  // const showDisabled = props.isDragActive && !canDrop; // Only show disabled state during drag

  // Get drag feedback for this stage
  const stageFeedback = props.dragFeedback?.target_stages[props.stage.id];
  const dragStatus = stageFeedback?.status || 'allowed';
  
  // Determine column classes based on drag status
  const getColumnClasses = () => {
    let classes = 'kanban-column';
    
    if (isDragOver) {
      classes += ' drag-over';
    }
    
    if (props.isDragActive) {
      classes += ' drag-active';
      
      switch (dragStatus) {
        case 'allowed':
          classes += ' drag-target-allowed';
          break;
        case 'blocked':
          classes += ' drag-target-blocked';
          break;
        case 'warning':
          classes += ' drag-target-warning';
          break;
      }
    }
    
    return classes;
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    
    if (dragStatus === 'blocked') {
      e.dataTransfer.dropEffect = 'none';
      return;
    }
    
    e.dataTransfer.dropEffect = 'move';
    
    if (!isDragOver) {
      setIsDragOver(true);
    }

    // Show tooltip if enabled
    if (props.showTooltip && stageFeedback && (stageFeedback.reason || stageFeedback.message)) {
      setTooltipPosition({ x: e.clientX, y: e.clientY });
      setTooltipVisible(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent) => {
    // Only set drag over to false if we're actually leaving the column
    const rect = e.currentTarget.getBoundingClientRect();
    const x = e.clientX;
    const y = e.clientY;
    
    if (x < rect.left || x > rect.right || y < rect.top || y > rect.bottom) {
      setIsDragOver(false);
      setTooltipVisible(false);
    }
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
    setTooltipVisible(false);
    
    if (dragStatus === 'blocked') {
      return; // Ignore drop if not allowed
    }
    
    const dealId = e.dataTransfer.getData('text/plain');
    if (dealId) {
      props.onDrop(dealId, props.stage.id);
    }
  };

  // Get role indicator for the current user
  const getRoleIndicator = () => {
    if (!props.userInfo) return null;
    
    const roleClass = props.userInfo.role.toLowerCase().replace('_', '-');
    const roleInitial = props.userInfo.role.charAt(0).toUpperCase();
    
    return (
      <div className={`role-indicator ${roleClass}`}>
        {roleInitial}
      </div>
    );
  };

  // Get tooltip content
  const getTooltipContent = () => {
    if (!stageFeedback) return null;
    
    if (stageFeedback.reason) {
      return stageFeedback.reason;
    }
    
    if (stageFeedback.message) {
      return stageFeedback.message;
    }
    
    return null;
  };

  const tooltipContent = getTooltipContent();

  return (
    <>
      <div 
        className={getColumnClasses()}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        style={{
          transition: 'all 0.2s ease-in-out',
          opacity: dragStatus === 'blocked' && props.isDragActive ? 0.7 : 1
        }}
      >
        <div 
          className="kanban-column-header" 
          style={{ backgroundColor: props.stage.color || '#3498db' }}
        >
          {props.stage.name} ({props.deals.length})
          {dragStatus === 'blocked' && props.isDragActive && <span className="lock-icon" style={{ marginLeft: '8px', opacity: 0.7 }}></span>}
          {getRoleIndicator()}
        </div>
        <div className="kanban-column-body">
          {props.children}
        </div>
      </div>
      
      {/* Tooltip */}
      {tooltipVisible && tooltipContent && (
        <div 
          className={`drag-tooltip show ${dragStatus === 'blocked' ? 'error' : dragStatus === 'warning' ? 'warning' : 'success'}`}
          style={{
            left: tooltipPosition.x + 10,
            top: tooltipPosition.y - 30
          }}
        >
          {tooltipContent}
        </div>
      )}
    </>
  )
}

interface KanbanComponentProps {
  stages: Stage[],
  deals: Deal[],
  height?: number,
  allow_empty_stages?: boolean,
  draggable_stages?: string[],
  user_info?: UserInfo,
  drag_restrictions?: DragRestrictions,
  permission_matrix?: PermissionMatrix,
  business_rules?: BusinessRule[],
  show_tooltips?: boolean
}

function KanbanComponent(props: KanbanComponentProps) {
  const [deals, setDeals] = useState(props.deals || []);
  const [stages] = useState(props.stages || []);
  const [draggedDealId, setDraggedDealId] = useState<string | null>(null);
  const [draggableStages] = useState(props.draggable_stages || []);
  const [isDragActive, setIsDragActive] = useState(false);
  const [dragRestrictions] = useState(props.drag_restrictions || {});
  const [userInfo] = useState(props.user_info);
  const [currentDragFeedback, setCurrentDragFeedback] = useState<DragFeedback | null>(null);
  
  // NUOVO: Tracking per prevenire click dopo drop
  const [recentDropTime, setRecentDropTime] = useState<number | null>(null);
  const [recentDropDealId, setRecentDropDealId] = useState<string | null>(null);
  const DROP_CLICK_PREVENTION_TIME = 1000; // 1 secondo
  
  // Filter and sort state
  const [filters, setFilters] = useState<FilterOptions>({
    priority: [],
    source: [],
    risk_rating: [],
    product_type: [],
    underwriter: [],
    amount_range: { min: 0, max: 1000000 },
    visible_stages: stages.filter(stage => 
      !['rejected', 'deleted'].includes(stage.id.toLowerCase())
    ).map(stage => stage.id),
    search: ''
  });
  const [sortOptions, setSortOptions] = useState<SortOptions>({
    field: 'date',
    direction: 'desc'
  });
  const [filteredAndSortedDeals, setFilteredAndSortedDeals] = useState<Deal[]>([]);
  
  const [permissionEngine] = useState(() => {
    if (props.permission_matrix && props.user_info) {
      return new PermissionEngine(
        props.permission_matrix,
        props.business_rules || [],
        props.stages || []
      );
    }
    return null;
  });

  // Filter and sort functions
  const applyFilters = useCallback((dealsToFilter: Deal[], filterOptions: FilterOptions): Deal[] => {
    return dealsToFilter.filter(deal => {
      // Smart Search filter - search company name and tenant ID fields
      if (filterOptions.search.length > 0) {
        const searchTerm = filterOptions.search.toLowerCase();
        const searchFields = [
          deal.company_name || '',
          deal.deal_id || '',
          deal.id || '',
          deal.tenant_id || '',
          deal.assigned_to || ''
        ];
        
        const matchesSearch = searchFields.some(field => 
          field.toLowerCase().includes(searchTerm) || field.toLowerCase() === searchTerm
        );
        
        if (!matchesSearch) {
          return false;
        }
      }
      
      // Priority filter
      if (filterOptions.priority.length > 0 && !filterOptions.priority.includes(deal.priority || '')) {
        return false;
      }
      
      // Source filter
      if (filterOptions.source.length > 0 && !filterOptions.source.includes(deal.source || '')) {
        return false;
      }
      
      // Risk rating filter
      if (filterOptions.risk_rating.length > 0 && !filterOptions.risk_rating.includes(deal.risk_rating || '')) {
        return false;
      }
      
      // Product type filter
      if (filterOptions.product_type.length > 0 && !filterOptions.product_type.includes(deal.product_type || '')) {
        return false;
      }
      
      // Underwriter filter
      if (filterOptions.underwriter.length > 0 && !filterOptions.underwriter.includes(deal.underwriter || '')) {
        return false;
      }
      
      // Amount range filter
      const dealAmount = deal.amount || 0;
      if (dealAmount < filterOptions.amount_range.min || dealAmount > filterOptions.amount_range.max) {
        return false;
      }
      
      return true;
    });
  }, []);

  const applySorting = useCallback((dealsToSort: Deal[], sortingOptions: SortOptions): Deal[] => {
    return [...dealsToSort].sort((a, b) => {
      const { field, direction } = sortingOptions;
      
      let aValue: any = a[field as keyof Deal];
      let bValue: any = b[field as keyof Deal];
      
      // Handle different field types
      if (field === 'amount') {
        aValue = aValue || 0;
        bValue = bValue || 0;
      } else if (field === 'date') {
        aValue = new Date(aValue || 0);
        bValue = new Date(bValue || 0);
      } else if (field === 'priority') {
        // Priority ordering: high > medium > low
        const priorityOrder = { 'high': 3, 'medium': 2, 'low': 1 };
        aValue = priorityOrder[aValue as keyof typeof priorityOrder] || 0;
        bValue = priorityOrder[bValue as keyof typeof priorityOrder] || 0;
      } else {
        // String comparison for other fields
        aValue = String(aValue || '').toLowerCase();
        bValue = String(bValue || '').toLowerCase();
      }
      
      if (aValue < bValue) return direction === 'asc' ? -1 : 1;
      if (aValue > bValue) return direction === 'asc' ? 1 : -1;
      return 0;
    });
  }, []);

  // Update filtered and sorted deals whenever deals, filters, or sortOptions change
  useEffect(() => {
    let processedDeals = deals;
    
    // Apply filters
    processedDeals = applyFilters(processedDeals, filters);
    
    // Apply sorting
    processedDeals = applySorting(processedDeals, sortOptions);
    
    setFilteredAndSortedDeals(processedDeals);
  }, [deals, filters, sortOptions, applyFilters, applySorting]);

  // NUOVO: Sistema per tracciare aggiornamenti ottimistici
  const [pendingUpdates, setPendingUpdates] = useState<PendingUpdate[]>([]);
  const lastStreamlitUpdate = useRef<number>(0);
  const OPTIMISTIC_UPDATE_TIMEOUT = 2000; // 2 secondi

  // MIGLIORATA: Sincronizzazione intelligente con Streamlit
  useEffect(() => {
    console.log(`React: Received ${props.deals?.length || 0} deals from Streamlit`);
    
    const now = Date.now();
    const incomingDeals = props.deals || [];
    
    // Pulisci aggiornamenti ottimistici scaduti
    const validPendingUpdates = pendingUpdates.filter(
      update => now - update.timestamp < OPTIMISTIC_UPDATE_TIMEOUT
    );
    
    if (validPendingUpdates.length !== pendingUpdates.length) {
      setPendingUpdates(validPendingUpdates);
    }
    
    // Se non ci sono aggiornamenti ottimistici pendenti, sincronizza direttamente
    if (validPendingUpdates.length === 0) {
      console.log('React: No pending updates, syncing directly with Streamlit');
      setDeals(incomingDeals);
      lastStreamlitUpdate.current = now;
      return;
    }
    
    // Controlla se i dati di Streamlit riflettono i nostri aggiornamenti ottimistici
    const streamlitReflectsOptimisticUpdates = validPendingUpdates.every(update => {
      const dealInStreamlit = incomingDeals.find(d => d.id === update.dealId);
      return dealInStreamlit && dealInStreamlit.stage === update.toStage;
    });
    
    if (streamlitReflectsOptimisticUpdates) {
      console.log('React: Streamlit data reflects optimistic updates, syncing');
      setDeals(incomingDeals);
      setPendingUpdates([]); // Pulisci tutti gli aggiornamenti pendenti
      lastStreamlitUpdate.current = now;
    } else {
      console.log('React: Streamlit data does not reflect optimistic updates yet, keeping local state');
      // Mantieni lo stato locale ma aggiorna i deal che non sono stati modificati ottimisticamente
      const updatedDeals = deals.map(localDeal => {
        const hasPendingUpdate = validPendingUpdates.some(update => update.dealId === localDeal.id);
        if (hasPendingUpdate) {
          return localDeal; // Mantieni la versione locale
        }
        
        // Aggiorna con i dati di Streamlit per deal non modificati
        const streamlitDeal = incomingDeals.find(d => d.id === localDeal.id);
        return streamlitDeal || localDeal;
      });
      
      // Aggiungi nuovi deal da Streamlit che non esistono localmente
      const newDeals = incomingDeals.filter(streamlitDeal => 
        !deals.some(localDeal => localDeal.id === streamlitDeal.id)
      );
      
      setDeals([...updatedDeals, ...newDeals]);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.deals]); // Rimuovi pendingUpdates e deals dalle dipendenze per evitare loop

  // Cleanup automatico degli aggiornamenti ottimistici scaduti
  useEffect(() => {
    const interval = setInterval(() => {
      const now = Date.now();
      setPendingUpdates(prev => 
        prev.filter(update => now - update.timestamp < OPTIMISTIC_UPDATE_TIMEOUT)
      );
    }, 1000);
    
    return () => clearInterval(interval);
  }, []);

  // Group deals by stage using filtered and sorted deals
  const dealsByStage = stages.reduce((acc, stage) => {
    acc[stage.id] = filteredAndSortedDeals.filter(deal => deal.stage === stage.id);
    return acc;
  }, {} as Record<string, Deal[]>);

  // Filter and sort event handlers
  const handleFilterChange = useCallback((newFilters: FilterOptions) => {
    setFilters(newFilters);
  }, []);

  const handleSortChange = useCallback((newSortOptions: SortOptions) => {
    setSortOptions(newSortOptions);
  }, []);

  const handleFilterReset = useCallback(() => {
    setFilters({
      priority: [],
      source: [],
      risk_rating: [],
      product_type: [],
      underwriter: [],
      amount_range: { min: 0, max: 1000000 },
      visible_stages: stages.filter(stage => 
        !['rejected', 'deleted'].includes(stage.id.toLowerCase())
      ).map(stage => stage.id),
      search: ''
    });
    setSortOptions({
      field: 'date',
      direction: 'desc'
    });
  }, [stages]);

  // Filter stages by visibility settings and empty stages
  const visibleStages = stages.filter(stage => {
    // First check if stage is in visible_stages filter
    if (!filters.visible_stages.includes(stage.id)) {
      return false;
    }
    
    // Then check if we should show empty stages
    if (!props.allow_empty_stages) {
      const stageDeals = dealsByStage[stage.id] || [];
      return stageDeals.length > 0;
    }
    
    return true;
  });

  // Check if a deal can be dragged from its current stage
  function canDragDeal(dealId: string): boolean {
    const deal = deals.find(d => d.id === dealId);
    if (!deal || !userInfo) {
      return false;
    }
    
    // Use permission engine if available
    if (permissionEngine) {
      const result = permissionEngine.canPerformAction(userInfo, 'drag_from', deal);
      return result.allowed;
    }
    
    // Fallback to basic checks - user must be able to drag FROM the current stage
    const rolePermissions = props.permission_matrix?.[userInfo.role];
    if (rolePermissions) {
      const stagePermissions = rolePermissions.stages[deal.stage];
      
      if (!stagePermissions || !stagePermissions.drag_from) {
        return false;
      }
    }
    
    // Also check if stage is in draggable stages (legacy support)
    if (draggableStages.length > 0 && !draggableStages.includes(deal.stage)) {
      return false;
    }
    
    // Check deal-specific restrictions
    const dealRestrictions = dragRestrictions[dealId];
    if (dealRestrictions && !dealRestrictions.draggable) {
      return false;
    }
    return true;
  }

  // Check if a deal can be dropped on a specific stage
  function canDropDeal(dealId: string, targetStageId: string): { allowed: boolean, reason?: string } {
    const deal = deals.find(d => d.id === dealId);
    if (!deal || !userInfo) return { allowed: false, reason: "Deal not found or user not authenticated" };
    
    // Don't allow dropping on the same stage
    if (deal.stage === targetStageId) return { allowed: false, reason: "Same stage" };
    
    // Use permission engine if available
    if (permissionEngine) {
      const result = permissionEngine.canPerformAction(userInfo, 'drag_to', deal, targetStageId);
      return { 
        allowed: result.allowed, 
        reason: result.reason || result.warning 
      };
    }
    
    // Fallback to basic checks
    if (!draggableStages.includes(targetStageId)) {
      return { allowed: false, reason: "Target stage not accessible" };
    }
    
    // Check deal-specific restrictions
    const dealRestrictions = dragRestrictions[dealId];
    if (dealRestrictions) {
      if (dealRestrictions.blocked_stages.includes(targetStageId)) {
        return { 
          allowed: false, 
          reason: dealRestrictions.reasons[targetStageId] || "Not authorized for this stage" 
        };
      }
      
      if (dealRestrictions.allowed_stages.length > 0 && 
          !dealRestrictions.allowed_stages.includes(targetStageId)) {
        return { 
          allowed: false, 
          reason: "Not authorized for this stage" 
        };
      }
    }
    
    return { allowed: true };
  }

  function handleDealClick(deal: Deal) {
    // NUOVO: Previeni click se  appena avvenuto un drop
    const now = Date.now();
    if (recentDropTime && recentDropDealId === deal.id && 
        now - recentDropTime < DROP_CLICK_PREVENTION_TIME) {
      console.log(`React: Click prevented for deal ${deal.id} - recent drop detected`);
      return;
    }
    
    console.log(`React: Deal clicked: ${deal.id}`);
    Streamlit.setComponentValue({
      moved_deal: null,
      clicked_deal: deal,
      validation_error: null
    });
  }

  function handleDragStart(dealId: string) {
    setDraggedDealId(dealId);
    setIsDragActive(true);
    
    // Generate drag feedback if permission engine is available
    if (permissionEngine && userInfo) {
      const deal = deals.find(d => d.id === dealId);
      if (deal) {
        const feedback = permissionEngine.generateDragFeedback(deal, userInfo);
        setCurrentDragFeedback(feedback);
      }
    }
  }

  function handleDragEnd() {
    setDraggedDealId(null);
    setIsDragActive(false);
    setCurrentDragFeedback(null);
  }

  function handleDrop(dealId: string, targetStageId: string) {
    const dealToMove = deals.find(d => d.id === dealId);
    if (!dealToMove) {
      setDraggedDealId(null);
      setIsDragActive(false);
      return;
    }

    const originalStage = dealToMove.stage;
    
    // Validate the drop
    const validation = canDropDeal(dealId, targetStageId);
    
    if (!validation.allowed) {
      // Send validation error back to Streamlit
      setDraggedDealId(null);
      setIsDragActive(false);
      
      const validationError: ValidationError = {
        deal_id: dealId,
        from_stage: originalStage,
        to_stage: targetStageId,
        reason: validation.reason || "Move not allowed",
        blocked: true
      };
      
      console.log(`React: Validation failed for ${dealId}: ${validation.reason}`);
      Streamlit.setComponentValue({
        moved_deal: null,
        clicked_deal: null,
        validation_error: validationError
      });
      return;
    }
    
    // MIGLIORATO: Aggiornamento ottimistico con tracking
    console.log(`React: Optimistic update - moving ${dealId} from ${originalStage} to ${targetStageId}`);
    
    const now = Date.now();
    
    // 1. Aggiungi l'aggiornamento alla lista dei pending updates
    const newPendingUpdate: PendingUpdate = {
      dealId,
      fromStage: originalStage,
      toStage: targetStageId,
      timestamp: now
    };
    
    setPendingUpdates(prev => {
      // Rimuovi eventuali aggiornamenti precedenti per lo stesso deal
      const filtered = prev.filter(update => update.dealId !== dealId);
      return [...filtered, newPendingUpdate];
    });
    
    // 2. Aggiorna lo stato locale immediatamente
    const updatedDeals = deals.map(deal =>
      deal.id === dealId ? { ...deal, stage: targetStageId } : deal
    );
    setDeals(updatedDeals);
    
    // 3. Reset drag state
    setDraggedDealId(null);
    setIsDragActive(false);
    
    // NUOVO: Imposta il tracking del drop per prevenire click immediati
    setRecentDropTime(now);
    setRecentDropDealId(dealId);
    
    // Reset del tracking dopo il timeout
    setTimeout(() => {
      setRecentDropTime(null);
      setRecentDropDealId(null);
    }, DROP_CLICK_PREVENTION_TIME);
    
    // 4. Invia l'evento a Streamlit
    console.log(`React: Sending move event to Streamlit: ${dealId}  ${targetStageId}`);
    Streamlit.setComponentValue({
      moved_deal: {
        deal_id: dealId,
        from_stage: originalStage,
        to_stage: targetStageId
      },
      clicked_deal: null,
      validation_error: null
    });
  }

  useEffect(() => {
    if (props.height) {
      Streamlit.setFrameHeight(props.height);
    }
  }, [props.height]);

  return (
    <div className="kanban-board" style={{ height: props.height || 800, minHeight: 600 }}>
      <FilterPanel
        deals={deals}
        stages={stages}
        onFilterChange={handleFilterChange}
        onSortChange={handleSortChange}
        onReset={handleFilterReset}
      />
      <div className="kanban-columns">
        {visibleStages.map((stage) => {
          const stageDeals = dealsByStage[stage.id] || [];
          
          // Check if this stage can accept drops from the currently dragged deal
          const canDropHere = draggedDealId ? 
            canDropDeal(draggedDealId, stage.id).allowed : 
            draggableStages.includes(stage.id);
          
          return (
            <KanbanColumn 
              key={stage.id} 
              stage={stage} 
              deals={stageDeals}
              onDealClick={handleDealClick}
              onDrop={handleDrop}
              canDropHere={canDropHere}
              draggableStages={draggableStages}
              isDragActive={isDragActive}
              dragFeedback={currentDragFeedback || undefined}
              userInfo={userInfo}
              showTooltip={props.show_tooltips}
            >
              {stageDeals.map(deal => {
                const canDragFromHere = canDragDeal(deal.id);
                return (
                  <DealCard 
                    key={deal.id} 
                    deal={deal}
                    isDragging={deal.id === draggedDealId}
                    onClick={handleDealClick}
                    onDragStart={handleDragStart}
                    onDragEnd={handleDragEnd}
                    draggable={canDragFromHere}
                  />
                )
              })}
            </KanbanColumn>
          )
        })}
      </div>
      
      {/* Debug info per sviluppo */}
      {process.env.NODE_ENV === 'development' && (
        <div style={{ 
          position: 'fixed', 
          bottom: '10px', 
          right: '10px', 
          background: 'rgba(0,0,0,0.8)', 
          color: 'white', 
          padding: '10px', 
          borderRadius: '5px',
          fontSize: '12px',
          maxWidth: '300px'
        }}>
          <div>Pending Updates: {pendingUpdates.length}</div>
          {pendingUpdates.map((update, i) => (
            <div key={i}>
              {update.dealId}: {update.fromStage}  {update.toStage} 
              ({Math.round((Date.now() - update.timestamp) / 1000)}s ago)
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

function KanbanComponentWrapper(props: ComponentProps) {
  const stages = props.args["stages"];
  const deals = props.args["deals"]; 
  const height = props.args["height"];
  const allow_empty_stages = props.args["allow_empty_stages"];
  const draggable_stages = props.args["draggable_stages"];
  const user_info = props.args["user_info"];
  const drag_restrictions = props.args["drag_restrictions"];
  const permission_matrix = props.args["permission_matrix"];
  const business_rules = props.args["business_rules"];
  const show_tooltips = props.args["show_tooltips"];
  
  console.log(`KanbanComponentWrapper: Received ${deals?.length || 0} deals from Streamlit`);

  return (
    <KanbanComponent 
      stages={stages}
      deals={deals}
      height={height}
      allow_empty_stages={allow_empty_stages}
      draggable_stages={draggable_stages}
      user_info={user_info}
      drag_restrictions={drag_restrictions}
      permission_matrix={permission_matrix}
      business_rules={business_rules}
      show_tooltips={show_tooltips}
    />
  );
}

export default withStreamlitConnection(KanbanComponentWrapper); 