import { 
  Deal, 
  UserInfo, 
  PermissionMatrix, 
  BusinessRule, 
  Stage, 
  DragFeedback,
  RuleCondition,
  RuleAction,
  PermissionCondition 
} from './types';

export class PermissionEngine {
  private permissionMatrix: PermissionMatrix;
  private businessRules: BusinessRule[];
  private stages: Stage[];

  constructor(
    permissionMatrix: PermissionMatrix,
    businessRules: BusinessRule[] = [],
    stages: Stage[] = []
  ) {
    this.permissionMatrix = permissionMatrix;
    this.businessRules = businessRules;
    this.stages = stages;
  }

  /**
   * Check if user can perform a specific action on a deal
   */
  canPerformAction(
    user: UserInfo,
    action: string,
    deal: Deal,
    targetStage?: string
  ): { allowed: boolean; reason?: string; warning?: string } {
    // Get user's role permissions
    const rolePermissions = this.permissionMatrix[user.role];
    if (!rolePermissions) {
      return { allowed: false, reason: 'Invalid user role' };
    }

    // Check general action permissions
    if (action in rolePermissions.actions) {
      const actionAllowed = rolePermissions.actions[action as keyof typeof rolePermissions.actions];
      if (!actionAllowed) {
        return { allowed: false, reason: `Role ${user.role} cannot perform ${action}` };
      }
    }

    // Check stage-specific permissions
    if (targetStage) {
      const stagePermissions = rolePermissions.stages[targetStage];
      if (!stagePermissions) {
        return { allowed: false, reason: `No permissions for stage ${targetStage}` };
      }

      // Check if user can drag to this stage
      if (action === 'drag_to' && !stagePermissions.drag_to) {
        return { allowed: false, reason: 'Cannot drag deals to this stage' };
      }

      // Check if user can drag from current stage  
      if (action === 'drag_from') {
        const currentStagePermissions = rolePermissions.stages[deal.stage];
        if (!currentStagePermissions?.drag_from) {
          return { allowed: false, reason: `Cannot drag deals from ${deal.stage} stage` };
        }
      }

      // Check approval permissions
      if (action === 'approve' && !stagePermissions.approve) {
        return { allowed: false, reason: 'Cannot approve deals in this stage' };
      }

      // Check conditions
      if (stagePermissions.conditions) {
        const conditionResult = this.evaluateConditions(stagePermissions.conditions, deal, user);
        if (!conditionResult.allowed) {
          return conditionResult;
        }
      }
    }

    // For drag_from action, check current stage permissions
    if (action === 'drag_from') {
      const currentStagePermissions = rolePermissions.stages[deal.stage];
      if (!currentStagePermissions?.drag_from) {
        return { allowed: false, reason: `Cannot drag deals from ${deal.stage} stage` };
      }
    }

    // Check business rules
    const ruleResult = this.evaluateBusinessRules(deal, user, action, targetStage);
    if (!ruleResult.allowed) {
      return ruleResult;
    }

    // Check amount-based approvals
    if (action === 'approve' && deal.amount && deal.source) {
      const approvalResult = this.checkApprovalLimits(user, deal);
      if (!approvalResult.allowed) {
        return approvalResult;
      }
    }

    return { allowed: true };
  }

  /**
   * Check approval limits based on deal amount and source
   */
  private checkApprovalLimits(
    user: UserInfo,
    deal: Deal
  ): { allowed: boolean; reason?: string; warning?: string } {
    if (!deal.amount || !deal.source || !user.approval_limits) {
      return { allowed: true };
    }

    const currency = deal.currency || 'EUR';
    const userLimit = user.approval_limits[deal.source]?.[currency];
    
    if (userLimit === undefined) {
      return { allowed: false, reason: `No approval limit set for ${deal.source} deals in ${currency}` };
    }

    if (deal.amount > userLimit) {
      return { 
        allowed: false, 
        reason: `Deal amount ${deal.amount} ${currency} exceeds approval limit ${userLimit} ${currency}` 
      };
    }

    // Add warning for deals close to limit
    if (deal.amount > userLimit * 0.9) {
      return { 
        allowed: true, 
        warning: `Deal amount is close to your approval limit (${userLimit} ${currency})` 
      };
    }

    return { allowed: true };
  }

  /**
   * Evaluate permission conditions
   */
  private evaluateConditions(
    conditions: PermissionCondition[],
    deal: Deal,
    user: UserInfo
  ): { allowed: boolean; reason?: string } {
    for (const condition of conditions) {
      const dealValue = deal[condition.field];
      const result = this.evaluateCondition(condition, dealValue, user);
      
      if (!result) {
        return { 
          allowed: false, 
          reason: `Condition failed: ${condition.field} ${condition.operator} ${condition.value}` 
        };
      }
    }

    return { allowed: true };
  }

  /**
   * Evaluate business rules
   */
  private evaluateBusinessRules(
    deal: Deal,
    user: UserInfo,
    action: string,
    targetStage?: string
  ): { allowed: boolean; reason?: string; warning?: string } {
    // Sort rules by priority (higher priority first)
    const sortedRules = this.businessRules
      .filter(rule => rule.is_active)
      .sort((a, b) => b.priority - a.priority);

    for (const rule of sortedRules) {
      const conditionsMet = rule.conditions.every(condition => 
        this.evaluateRuleCondition(condition, deal, user, action, targetStage)
      );

      if (conditionsMet) {
        const result = this.executeRuleActions(rule.actions, deal, user, action, targetStage);
        if (result.blocked) {
          return { allowed: false, reason: result.reason };
        }
        if (result.warning) {
          return { allowed: true, warning: result.warning };
        }
      }
    }

    return { allowed: true };
  }

  /**
   * Evaluate a single rule condition
   */
  private evaluateRuleCondition(
    condition: RuleCondition,
    deal: Deal,
    user: UserInfo,
    action: string,
    targetStage?: string
  ): boolean {
    let fieldValue: any;

    // Handle special fields
    if (condition.field === 'user_role') {
      fieldValue = user.role;
    } else if (condition.field === 'action') {
      fieldValue = action;
    } else if (condition.field === 'target_stage') {
      fieldValue = targetStage;
    } else if (condition.field === 'source' && condition.source_specific) {
      fieldValue = deal.source;
    } else {
      fieldValue = deal[condition.field];
    }

    return this.evaluateCondition(
      { field: condition.field, operator: condition.operator, value: condition.value },
      fieldValue,
      user
    );
  }

  /**
   * Execute rule actions
   */
  private executeRuleActions(
    actions: RuleAction[],
    deal: Deal,
    user: UserInfo,
    action: string,
    targetStage?: string
  ): { blocked: boolean; reason?: string; warning?: string } {
    for (const ruleAction of actions) {
      switch (ruleAction.type) {
        case 'deny':
          return { blocked: true, reason: ruleAction.message || 'Action denied by business rule' };
        case 'warning':
          return { blocked: false, warning: ruleAction.message || 'Action has warnings' };
        case 'allow':
          return { blocked: false };
        // Add more action types as needed
      }
    }

    return { blocked: false };
  }

  /**
   * Generic condition evaluation
   */
  private evaluateCondition(
    condition: { field: string; operator: string; value: any },
    fieldValue: any,
    user: UserInfo
  ): boolean {
    switch (condition.operator) {
      case 'equals':
        return fieldValue === condition.value;
      case 'greater_than':
        return fieldValue > condition.value;
      case 'less_than':
        return fieldValue < condition.value;
      case 'in':
        return Array.isArray(condition.value) && condition.value.includes(fieldValue);
      case 'not_in':
        return Array.isArray(condition.value) && !condition.value.includes(fieldValue);
      case 'contains':
        return typeof fieldValue === 'string' && fieldValue.includes(condition.value);
      default:
        return false;
    }
  }

  /**
   * Generate drag feedback for all stages
   */
  generateDragFeedback(deal: Deal, user: UserInfo): DragFeedback {
    const feedback: DragFeedback = {
      deal_id: deal.id,
      target_stages: {}
    };

    for (const stage of this.stages) {
      if (stage.id === deal.stage) {
        // Skip current stage
        continue;
      }

      const result = this.canPerformAction(user, 'drag_to', deal, stage.id);
      
      if (result.allowed) {
        feedback.target_stages[stage.id] = {
          status: result.warning ? 'warning' : 'allowed',
          message: result.warning
        };
      } else {
        feedback.target_stages[stage.id] = {
          status: 'blocked',
          reason: result.reason
        };
      }
    }

    return feedback;
  }

  /**
   * Check if user can view a stage
   */
  canViewStage(user: UserInfo, stageId: string): boolean {
    const rolePermissions = this.permissionMatrix[user.role];
    return rolePermissions?.stages[stageId]?.view ?? false;
  }

  /**
   * Get filtered stages for user
   */
  getVisibleStages(user: UserInfo): Stage[] {
    return this.stages.filter(stage => this.canViewStage(user, stage.id));
  }

  /**
   * Get draggable stages for user
   */
  getDraggableStages(user: UserInfo): string[] {
    const rolePermissions = this.permissionMatrix[user.role];
    if (!rolePermissions) return [];

    return Object.keys(rolePermissions.stages).filter(stageId => 
      rolePermissions.stages[stageId].drag_to
    );
  }

  /**
   * Update permission matrix
   */
  updatePermissionMatrix(newMatrix: PermissionMatrix): void {
    this.permissionMatrix = newMatrix;
  }

  /**
   * Update business rules
   */
  updateBusinessRules(newRules: BusinessRule[]): void {
    this.businessRules = newRules;
  }

  /**
   * Update stages
   */
  updateStages(newStages: Stage[]): void {
    this.stages = newStages;
  }
} 